import WucTab from '@/components/wuc-tab/wuc-tab.vue';
export default {
	data() {
		return {
		  TabCur: 0,
		  tabList: [{ name: '我是会员' ,icon: 'cuIcon-comment'}, { name: '我是会头',icon: 'cuIcon-comment' }],
		  isHuiItem:true,
		}},
	components: { WucTab },
	methods: {
		tabChange(index) {
			if(index == 0){
				this.isHuiItem = true;
				return ;
			}
			this.isHuiItem = false;
		},
		toShare(){
			uni.navigateTo({
				url: '/pages/share/index'
			});
		}
	}
}
